.input {
  border: 1px solid #ccc;
  border-radius: 0.2rem;
  padding: 4px;
  margin-bottom: -4px;
  overflow-y: auto;
}

.ProseMirror {
  width: 100%;
  height: 100%;
  margin-top: -16px;

  // Blinking cursor is black
  .ProseMirror-cursor {
    border-left: 1px solid black;
  }

  // Hide outline when focused
  &:focus {
    outline: none;
  }

  ul,
  ol {
    padding: 0 1rem;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 1.1;
  }

  code {
    background-color: rgba(#616161, 0.1);
    color: #616161;
  }

  pre {
    // VS-Code dark mode
    background-color: #1e1e1e;
    color: #d4d4d4;
    font-family: 'Fira Code', monospace;
    padding: 0.5rem 0.75rem;
    border-radius: 0.2rem;

    code {
      color: inherit;
      padding: 0;
      background: none;
      font-size: 0.8rem;
    }
  }

  img {
    max-width: 100%;
    height: auto;
  }

  blockquote {
    padding-left: 1rem;
    border-left: 2px solid rgba(#0d0d0d, 0.1);
  }

  hr {
    border: none;
    border-top: 2px solid rgba(#0d0d0d, 0.1);
    margin: 2rem 0;
  }
}